.card {

    position: relative;
    width:600px;
    padding:20px;
    max-width: 85vh;
    height: 50vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    justify-content:center;
    box-shadow: 0px 18px 53px 0px rgba(0,0,0,0.3);

}

.card > h3 {

    position:absolute;
    color:white;
    bottom:20px;
    font-size: 50px;

}


.cardContainer{
    
    display:flex;
    justify-content:center;
    margin: 5vh;

}

.swipe {

    position: absolute;

}